import './css/navbar.css';
import Logo from './images/logo.png';
import { Link as RouterLink } from 'react-router-dom';
import { useEffect,useRef  } from "react";
import $ from 'jquery';

function NavbarUser(){

    useEffect(() => {
        $("#open-nav").on("click", function() {
            $(".nav-eles").slideDown(400);
            $("nav").addClass("active");

            $(".nav-eles").css({display: "flex"});
        });

        $("#close-nav").on("click", function() {
            $(".nav-eles").slideUp(400);
            $("nav").removeClass("active");

        });

        if ($(window).width() < 950) {
            $(".nav-eles li").on("click", function() {
                $(".nav-eles").slideUp(400);
                $("nav").removeClass("active");

            });
        }
    
    })




    return(
        <>
            <nav>
                <div className='container-fluid'>
                    <div className='nav-content'>
                        <div className='last-login'>

                            <h2><strong>Welcome : </strong> Anuj Kumar </h2>
                            <h2><strong> Last Login : </strong> ::1 <strong>at</strong> 2018-06-21 15:41:01</h2>

                        </div>
                        <div className='logo'>
                            <RouterLink to="/user-home-page">
                                <img src={Logo} alt="logo" />
                            </RouterLink>
                        </div>

                        <button id="open-nav"> <i className="las la-bars"></i> </button>
                        <ul className="nav-eles">
                        <button id="close-nav"> <i className="las la-times"></i> </button>

                            <li>
                                <RouterLink to="/user-home-page">
                                    Home
                                </RouterLink>
                            </li>



                            <li>
                                <RouterLink to="/enroll-for-course">
                                    Enroll for Course
                                </RouterLink>
                            </li>

                            <li>
                                <RouterLink to="/enroll-user-history">
                                    Enroll History
                                </RouterLink>
                            </li>

                            <li>
                                <RouterLink to="/user-profile">
                                    My Profile
                                </RouterLink>
                            </li>

                            <li>
                                <RouterLink to="/change-user-password">
                                    Chnage Password
                                </RouterLink>
                            </li>

                            <li>
                                <RouterLink to="/">
                                    Logout
                                </RouterLink>
                            </li>



                        </ul>
                    </div>
                </div>
            </nav>
        </>
    )
}

export default NavbarUser;