import  './css/pagesinputs.css';
import { Link as RouterLink } from 'react-router-dom';
import UserImg from './images/users/user.webp';

function CourseEnroll(){
    return(
        <>
            <section className='form-inputs'>
                <div className='container-fluid'>
                    <div className='section-header'>
                        <h1> COURSE ENROLL </h1>
                    </div>

                    <div className='form-inputs-content'>
                        <form action='' method=''>
                            <h1> Course Enroll </h1>


                            <div className='input'>
                                <label> Student Name </label>
                                <input defaultValue="Anuj Kumar" readOnly type='text' required name='student_name' />
                            </div>

                            <div className='input'>
                                <label> Student Reg No </label>
                                <input defaultValue="1080528" readOnly type='text' required name='student_reg_no' />
                            </div>

                            
                            <div className='input'>
                                <label> Pincode </label>
                                <input defaultValue="35885" readOnly type='number' required name='student_reg_no' />
                            </div>

                            <div className='student-photo input'>
                                <label> Student Photo </label>
                                <img src={UserImg} alt="user image"/>
                            </div>


                            
                            <div className='input'>
                                <label> Session </label>
                                <select name='session' required >
                                    <option value="" > Select Session </option>
                                    <option value="Session 1" >  Session 1 </option>
                                    <option value="Session 2" > Session 2</option>
                                    <option value="Session 3" > Session 3 </option>
                                    <option value="Session 4" > Session 4 </option>

                                </select>
                            </div>


                            <div className='input'>
                                <label> Level </label>
                                <select name='level' required >
                                    <option value="" > Select Level </option>
                                    <option value="Level 1" >  Level 1 </option>
                                    <option value="Level 2" > Level 2</option>
                                    <option value="Level 3" > Level 3 </option>
                                    <option value="Level 4" > Level 4 </option>
                                    <option value="Level 5" > Level 5 </option>

                                </select>
                            </div>

                            <div className='input'>
                                <label> Semester </label>
                                <select name='semester' required >
                                    <option value="" >Select Semester </option>
                                    <option value="Semester 1" >  Semester 1 </option>
                                    <option value="Semester 2" > Semester 2</option>
                                    <option value="Semester 3" > Semester 3 </option>
                                    <option value="Semester 4" > Semester 4 </option>

                                </select>
                            </div>


                            
                            <div className='input'>
                                <label> Course </label>
                                <select name='course' required >
                                    <option value="" > Seelct Course </option>
                                    <option value="Course 1" >  Course 1 </option>
                                    <option value="Course 2" > Course 2</option>
                                    <option value="Course 3" > Course 3 </option>
                                    <option value="Course 4" > Course 4 </option>

                                </select>
                            </div>


                            <button type='submit'> Enroll </button>
                        </form>
                    </div>

                </div>
            </section>
        </>
    )
}

export default CourseEnroll;