import  './css/pagesinputs.css';
import { Link as RouterLink } from 'react-router-dom';


function UpdateCourse(){
    return(
        <>
            <section className='form-inputs'>
                <div className='container-fluid'>
                    <div className='section-header'>
                        <h1> Update Course </h1>
                    </div>

                    <div className='form-inputs-content'>
                        <form action='' method=''>
                            <h1> Course </h1>


                            <div className='input'>
                                <label> Course Code</label>
                                <input defaultValue="PHP01" placeholder='Course Code' type='text' required name='course_code' />
                            </div>

                            
                            <div className='input'>
                                <label> Course Name</label>
                                <input defaultValue="Core PHP"  placeholder='Course Name' type='text' required name='course_name' />
                            </div>


                            <div className='input'>
                                <label> Course Unit</label>
                                <input defaultValue="1.5" placeholder='Course Unit' type='text' required name='course_unit' />
                            </div>

                            <div className='input'>
                                <label> Seat Limit</label>
                                <input  defaultValue="10" placeholder='Seat Limit' type='number' required name='seat_limit' />
                            </div>

                            <button type='submit'> Update </button>
                        </form>
                    </div>

                </div>
            </section>
        </>
    )
}

export default UpdateCourse;